<template>
    <div class="result">
        <answer-pc-details :id="formId" :userId="userId" @onBack="back"></answer-pc-details>
    </div>
</template>
<script>
import AnswerPcDetails from '../components/AnswerPcDetails'

export default {
    name: 'Result',
    components: {AnswerPcDetails},
    data(){
        return{
           formId:this.$route.query.formId || '',
           userId:this.$route.query.userId || '',
           origin:this.$route.query.origin || ''
        }
    },
    methods:{
        back(){
            if(this.origin === 'answer'){
                window.history.back(-1)
                window.history.back(-1)
            }else{
                window.history.back(-1)
            }
           
        }
    }
}
</script>
<style lang="scss">
    .result{
        width:100%;
        height: 100%;
        background-color: #fff;
    }
</style>


